import { EnvService } from 'src/app/services/env.service';
import { ActivatedRoute } from '@angular/router';
import { take } from 'rxjs/operators';
import { AskQuestionComponent } from './../ask-question/ask-question.component';
import { AlertService } from 'src/app/services/alert.service';
import { PublicQuestionAndAnswersService } from './../../services/public-question-and-answers.service';
import { PublicQuestionAndAnswer } from './../../models/public-question-and-answer';
import { IonRefresher, NavController } from '@ionic/angular';
import { Component, OnInit } from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: 'app-questions-and-answers',
  templateUrl: './questions-and-answers.page.html',
  styleUrls: ['./questions-and-answers.page.scss'],
})
export class QuestionsAndAnswersPage implements OnInit {
  questions: PublicQuestionAndAnswer[] = [];
  byMeFilter = false;
  refresher:IonRefresher = null;
  roleId = null;
  enableNewQuestion = false;
  userId = null;
  constructor(
    private qaService:PublicQuestionAndAnswersService,
    private alert:AlertService,
    private navController:NavController,
    private route:ActivatedRoute,
    private env:EnvService
  ) { }

  ngOnInit() {
    this.userId = this.route.snapshot.params.userId;
    this.roleId = this.route.snapshot.params.roleId;
    if(this.roleId == this.env.USER_ROLE_ID)
    {
      this.enableNewQuestion = true;
    }
    this.qaService.questionsShared.getUpdateNotifier(this).subscribe(result=>{
      this.filterQuestions();
      if(this.refresher)
      {
        this.refresher.complete();
      }
    });
    this.qaService.questionsShared.request();
  }
  filterQuestions()
  {
    if(this.byMeFilter)
    {
      this.questions = this.qaService.questionsShared.resource().filter(q=>{
        return q.user_id == this.userId;
      })
    }
    else
    {
      this.questions = this.qaService.questionsShared.resource();
    }
  }
  refresh(event)
  {
    this.refresher = event.target;
    this.qaService.questionsShared.request();

  }
  ask()
  {
    this.alert.presentModal(AskQuestionComponent,{},true).pipe(take(1)).subscribe();
  }
  setFilterStatus(ev)
  {
    // this.selectedProviderFilterStatus = ev;
    if(ev.target.checked)
    {
      this.byMeFilter = true;
    }
    else
    {
      this.byMeFilter = false;

    }
    this.filterQuestions();
  }
  navigateToQuestion(questionId:number)
  {
    if(this.roleId && this.userId)
    {
      this.navController.navigateForward(['/answers'
        ,questionId, this.roleId, this.userId]);
    }
    
  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    
  }

}
